import React, { useState, useContext, useEffect } from 'react';
import { CartContext } from '../../CartContext/CartContext';
import '../ItemDetail/ItemDetail.css';
import { Link, useHistory, useParams } from 'react-router-dom';
import '../ItemCount/ItemCount.css';
import { dbVentas } from '../../CartContext/CartContext';
import firebase from 'firebase/app';
import Form from '../Form/Form';



const Cart = () => {
	const [orderId, setOrderId] = useState();
	const history = useHistory();
	const [cart, setCart] = useContext(CartContext);
	const [total, setTotal] = useState();


	useEffect(() => {
		let contador = 0;

		if (cart.length > 0) {
			cart.forEach(producto => {
				contador = Number(contador) + Number(producto.item.price);
				setTotal(contador);
			})
		}

	}, [])

	const registrarVenta = (buyer) => {

		let venta = {
			buyer,
			items: { cart },
			date: firebase.firestore.Timestamp.fromDate(new Date()),
			total: total
		};

		dbVentas.add(venta).then(({ id }) => {
			setOrderId(id);
			console.log(`El numero de orden es ${id}`)
			history.push(`/cart/checkout/${id}`)
		})


	}

	return (

		cart.length > 0 ? (
			<div className="cardcontainer" style={{ marginTop: '25px', display: 'flex', flexFlow: 'row', justifyContent: 'space-between', width: '75%' }}>
				<div className="col"  >

					<h2>Detalle de Carrito:</h2><br />
					{cart.map((articulo, index) => (
						<div key={index}>
							<div style={{ display: "flex", justifyContent: "center", marginBottom: "2rem" }}>
								<img src={articulo.item.img} width="25%" alt="laptop"></img>
								<div style={{ display: "flex", flexFlow: "column" }}>
									<h3 style={{ marginTop: "30px" }}>{articulo.item.name}</h3>
									<p>Cantidad: {articulo.quantity} unidad(es)</p>
									<p>${(articulo.item.price) * (articulo.quantity)}</p><br />

								</div>
							</div>
						</div>)

					)}
					<h5 className="text-primary">El total de su compra es $ {total}.</h5>
					{/* <Link to={'/cart/checkout'}><button type="submit" className="btn__carrito mt-4" >Comprar</button></Link> */}


				</div>
				<Form registrarVenta={registrarVenta} cart={cart} orderId={orderId} />

			</div >)

			: (
				<>

					<div className="cardcontainer" style={{ margin: '25px auto' }}>
						<Link to={`/`}><h3>Tu carrito está vacío.</h3><br /><button className="btn__carrito">Regresar al Inicio</button></Link>
					</div>
				</>
			)

	)
}

export default Cart;

