
import ItemCount from '../ItemCount/ItemCount';
import '../ItemCount/ItemCount.css';
import './ItemDetail.css';
import { useContext } from 'react';
import { CartContext } from '../../CartContext/CartContext';

const ItemDetail = ({ item }) => {
	const [cart, setCart] = useContext(CartContext);
	console.log(cart);


	const addToCart = (counter) => {
		alert(`Se agregaron ${counter} productos al carrito.`)
		setCart([...cart, {
			quantity: counter,
			item: item
		}]);
	}

	return (
		<>

			<div className="cardcontainer" style={{ margin: '25px auto' }}>
				<div className="col" style={{ margin: '0 auto' }} >

					<img src={item.img} alt="notebook" width="80%" ></img>
					<h2>{item.name}</h2>
					<p>${item.price}</p>
					<ItemCount initial={1} min={1} max={10} onAdd={addToCart} />

				</div>


			</div>




		</>
	)
}

export default ItemDetail;

