import React, { useState } from "react";
import './ItemCount.css';

const ItemCount = ({ initial, min, max, onAdd }) => {
    const [counter, setCounter] = useState(initial);

    const incrementCounter = () => {
        if (counter + 1 <= max) {
            setCounter(counter + 1)
        };

    };

    const decrementCounter = () => {
        if (counter - 1 >= (min)) {
            setCounter(counter - 1)
        }
    };

    return (
        <div>
            <div className="container">
                <button className="btn__decrement" onClick={decrementCounter}><span className="innerbutton">-</span>
                </button>
                <input className="btn__input" type="number" disabled value={counter} />
                <button className="btn__increment" onClick={incrementCounter}><span className="innerbutton">+</span>
                </button>
                <br />
                <br />
            </div>
            <div className="btn__container">
                <button className="btn__carrito-submit" onClick={() => onAdd(counter)} >
                    Comprar {counter}
                </button>
            </div>
        </div>
    );
};

export default ItemCount;
