import React, { useState, useEffect } from 'react';
import '../ItemCount/ItemCount.css';
import { useContext } from 'react';
import { CartContext } from '../../CartContext/CartContext';



const Form = ({ registrarVenta }) => {

	const [cart, setCart] = useContext(CartContext);
	const [name, setName] = useState("");
	const [email, setEmail] = useState("");
	const [telefono, setTelefono] = useState("");
	const [email2, setEmail2] = useState("");
	const [Hidden, setHidden] = useState("block")

	const handleChangeName = (event) => {
		setName(event.target.value);
	}
	const handleChangeEmail = (event) => {
		setEmail(event.target.value);
	}
	const handleChangeEmail2 = (event) => {
		setEmail2(event.target.value);
	}
	const handleChangeNumber = (event) => {
		setTelefono(event.target.value);
	}

	const handleSubmit = (event) => {
		event.preventDefault();
		successfulTransaction();
		setName("");
		setEmail("");
		setEmail2("");
		setTelefono("");
		setHidden("none");
		setCart([]);

	}

	const successfulTransaction = () => {
		const buyer = { name, telefono, email };
		registrarVenta(buyer);
	};


	return (
		<>
			<form onSubmit={handleSubmit} className='p-3 mr-4 border border-3' style={{ height: '460px', width: '30%', margin: '20px auto' }}>
				<h3 className='mb-4'>Registrar Compra</h3>
				<div className="mb-3">
					<input
						type="text"
						className="form-control"
						name="nombre"
						placeholder="Nombre y Apellido"
						onChange={handleChangeName}
						required
						value={name}
					/>

				</div>

				<div className="mb-3">
					<input
						type="text"
						className="form-control"
						name="telefono"
						placeholder="Teléfono"
						onChange={handleChangeNumber}
						required
						value={telefono}

					/>

				</div>

				<div className="mb-3">
					<input
						type="email"
						className="form-control"
						name="email"
						placeholder="Email"
						onChange={handleChangeEmail}
						required
						value={email}

					/>
				</div>

				<div className="mb-3">
					<input
						type="email"
						className="form-control"
						name="email2"
						placeholder="Repetir Email"
						onChange={handleChangeEmail2}
						required
						value={email2}
					/>
					{email.trim() !== email2.trim() ?
						(<span className="text-danger">Los emails no coinciden.</span>)
						:
						(null)
					}
				</div>
				<button type="submit" className="btn__carrito-submit mt-4" style={{ margin: '0 auto' }} >Finalizar Compra</button>

				{/* {orderId && <Redirect to={'/cart/checkout/${orderId}'} />} */}

			</form>

			{/* <div >
				<p className="text-success" >Compra exitosa! Su numero de orden es: <small><strong>{orderId}</strong></small></p>
				<Link to={'/'}><button className="btn__checkout">Seguir Comprando</button></Link>
			</div> */}
		</>

	)
}




export default Form;