import React, { useState, useEffect } from 'react';
import '../ItemCount/ItemCount.css';
import { dbVentas } from '../../CartContext/CartContext';
import { useContext } from 'react';
import { CartContext } from '../../CartContext/CartContext';
import Spinner from '../Spinner/Spinner';
import { useParams } from 'react-router-dom';
import { Link } from 'react-router-dom';

const Checkout = () => {
    const { orderNumber } = useParams();
    const [singleOrderId, setsingleOrderId] = useState();

    useEffect(() => {

        dbVentas.get().then((querySnapshot) => {
            const aux =
                querySnapshot.docs.map((doc) => {
                    return { id: doc.id, ...doc.data() };
                })

            let singleOrderId = aux.find(element => (element.id) === (orderNumber));


            if (singleOrderId) {
                console.log(`El ID del producto seleccionado es: ${singleOrderId.id}`)
                setsingleOrderId(singleOrderId);

            } else {
                alert(`No se encontraron productos`);

            }
        })

    }, [orderNumber])

    return (
        <>
            {singleOrderId ? (
                <>
                    <div className="text-center mt-5">
                        <p style={{ fontSize: '1.50rem' }}>Gracias por su compra! Su numero de orden es:  </p>
                        <p style={{ color: '#0D6EFD', fontSize: '1.25rem', fontWeight: 'bold' }}>{singleOrderId.id}</p>
                        <Link to={'/'}><button className="btn__carrito-submit mt-4" style={{ margin: '0 auto' }} >Regresar al Home</button></Link>
                    </div>

                </>)

                :
                (<Spinner />)}

        </>

    )
}


export default Checkout;