import Item from '../Item/Item';
import Spinner from '../Spinner/Spinner';

const ItemList = ({ items }) => {

    if (!items) {
        return <Spinner />
    }

    return (
        <div style={{ background: '#fff', borderRadius: '5px' }}>
            { items.map((item, index) => <Item item={item} key={index} />)}
        </div>
    )
}

export default ItemList;